.feather{width:16px;height:16px;vertical-align:text-bottom}.feather.ico{height:30px;width:35px;color:#007bff}.sidebar{position:fixed;top:0;bottom:0;left:0;z-index:100;padding:0;box-shadow:inset -1px 0 0 rgba(0,0,0,0.1)}.sidebar-sticky{position:-webkit-sticky;position:sticky;top:48px;height:calc(100vh - 48px);padding-top:1rem;overflow-x:hidden;overflow-y:auto}.sidebar .nav-link{font-weight:500;color:#333}.sidebar .nav-link .feather{margin-right:4px;color:#999}.sidebar .nav-link.active{color:#007bff}.sidebar .nav-link:hover .feather,.sidebar .nav-link.active .feather{color:inherit}.sidebar-heading{font-size:.75rem;text-transform:uppercase}.navbar-brand{padding-top:.75rem;padding-bottom:.75rem;font-size:1rem;background-color:rgba(0,0,0,0.25);box-shadow:inset -1px 0 0 rgba(0,0,0,0.25)}.navbar .form-control{padding:.75rem 1rem;border-width:0;border-radius:0}.form-control-dark{color:#fff;background-color:rgba(255,255,255,0.1);border-color:rgba(255,255,255,0.1)}.form-control-dark:focus{border-color:transparent;box-shadow:0 0 0 3px rgba(255,255,255,0.25)}.border-top{border-top:1px solid #e5e5e5}.border-bottom{border-bottom:1px solid #e5e5e5}.activities .activities-w{width:100%;overflow:hidden;border-radius:3px;border:1px solid #dfe0e1}.activities .card .big-f{font-size:3em;font-weight:100}.profile-card .profile-card-w{width:100%;overflow:hidden;border-radius:3px;border:1px solid #dfe0e1}.profile-card .profile-card-w .profile-bg{width:100%;height:140px;background:url("../images/full-bg.svg") no-repeat center;background-size:cover}.profile-card .profile-card-w .profile-detail{position:relative}.profile-card .profile-card-w .profile-detail .profile-gravatar{width:120px;height:120px;background:#f8f9fa;margin:0 auto;position:relative;top:-40px;border:3px solid #fff;border-radius:100%;box-shadow:0 1px 1px rgba(0,0,0,0.1)}.profile-card .profile-card-w .profile-detail .details{margin-top:-20px}.profile-card .profile-card-w .profile-detail .details .profile-name{text-transform:uppercase;font-size:.85em}
